import React, {useState, useEffect} from 'react';
import axios from 'axios';

export default function Signup(props) {
    //function onSignup(){
    //   
    const onSignup = () =>{
        console.log('onSignup open')
        let info = {
            memail : document.querySelector('.memail').value,
            mpassword : document.querySelector('.mpassword').value,
            mname : document.querySelector('.mname').value,
            mphone : document.querySelector('.mphone').value,
        }
        console.log(info)
        // ajax -> axios
        axios.post("/member/info" , info)
            .then( r =>{
                console.log(r);
                if(r.data==1){
                    alert(' ');
                    window.location.href = "/member/login";
                }else if(r.data==2){
                    alert(' ');
                }else if(r.data==3){
                    alert('  .');
                    window.location.href="/login"
                }
            })
            .catch(err=>{console.log(err)});
    }
    // 2.  
    let [ memailMsg , setMemailMsg ] = useState('');
    const idCheck = (e) => {  // 1. console.log( document.querySelector('.memail').value ) ;  // 2. console.log( e.target.value ) ;
        axios.get( "/member/idcheck" , { params : { memail : e.target.value } } )
            .then( res => {
                if( res.data == true ){ setMemailMsg('  .')}
                else{ setMemailMsg('    . ')}
                }
            )  .catch( e => console.log( e ) )
    }
    return (<>
            <h3> </h3>
            <div>
                [] : <input type="text" className="memail" onChange={ idCheck } />
                                    <span> { memailMsg } </span>
                                    <br/>
                 : <input type="password" className="mpassword" /> <br/>
                 : <input type="text" className="mname" /> <br/>
                 : <input type="text" className="mphone" /> <br/>
                <button onClick={onSignup} type="button" ></button>
            </div>
    </>)
}

/*
    HTML -> JSX
        1. <> </>
        2. class => className
        3.style => style={{}}
        4.  :
            onclick => onClick
            margin-top => marginTop

*/

