//  App  -> AppTodo 
import React, {useState, useEffect} from 'react';
import Todo from './Todo';
import AddTodo from './AddTodo';
import {List, Paper, Container} from '@mui/material';
import Pagination from '@mui/material/Pagination';
import axios from 'axios'; // npm install axios [ install -> i ]

export default function AppTodo(props) {


    const [ items, setItems ] = useState( [ ] )
    let [ page , setPage ] = useState(1);
    let [ totalPage , setTotalPage ] = useState(1);
    let [ totalCount , setTotalCount ] = useState(1);

    // 2. items  item  
    const addItem = (item) =>{
        setItems([...items, item ] ); //   items item 
        // setItems([...,   ])
        axios.post("/todo", { title : item.title }).then(r=>{console.log(r)});
    }

    //       
    useEffect( ()=>{
        // ajax : jquery  
        // fetch :      [   /  X ]
        // axios :    [   ] ==> JSON  .
        axios.get("/todo/getTodo" , {page : page} )
                .then(r=>{
                        console.log(r)
                        setItems(r.data.todoDtoList);
                        setTotalCount(r.data.totalCount)
                        setTotalPage(r.data.totalPage)
                    }
                );
        console.log(setItems);
         //    / @CrossOrigin(origins = "http://localhost:3000") 
    }, [page])

    // 3. items item 
    const deleteItem = (item)=>{
    //  items  item  id  id    item 
        const newItems = items.filter( i => i.id !== item.id);
        console.log(item.id);
        axios.delete("/todo", {params : { id : item.id }}).then(r=>{ console.log(r);});

            //  id  newItems 
        setItems([...newItems]);
    }
    // JS   
                // /.forEach((o)=>{}) :   ,  X
                // /.map((o)=>{}) : + return    
                // /.filter((o)=>{}) : +      

    // 4. 
    const editItem = ()=>{
        setItems([...items]); // 
    }
    // 5.   
    const selectPage = (e) =>{
        //console.log(e);
        //console.log(e.target);
        //console.log(e.target.value) // button   value  
        //console.log(e.target.innerHTML); //  button   html 
        console.log(e.target.outerText); //  button     text 
        page = e.target.outerText;
        setPage({...page});
    }

    //   Todo  
    //JSX style  
    let TodoItems =
        <Paper style={{margin : 16}}>
            <List>
                {
                    items.map( (i)=>
                        <Todo
                            item ={ i }
                            key = { i.id }
                            deleteItem = { deleteItem }
                            editItem = { editItem } />
                    )
                }
            </List>
        </Paper>

    return (<>
        <div className="App">
            <Container maxWidth="md">
                <AddTodo addItem={addItem}  />
                {TodoItems}
                <div style={ { display:'flex' , justifyContent : 'center', margin : '40px 0px' } }>
                    {/* count :    */}
                    <Pagination count={totalPage} color="primary" onClick={selectPage} />
                </div>
            </Container>
        </div>
    </>);
}