import React,{ useState , useEffect } from 'react';
import axios from 'axios';
/* ---------table mui -------- */
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Pagination from '@mui/material/Pagination';
/* ---------------------------*/
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import CategoryList from './CategoryList';

export default function List( props ) {
    // 1.       [   ]
    let [ rows , setRows ] = useState( [] )
    let [ pageInfo , setPageInfo ] = useState( { 'cno' : 0, 'page' : 1} );
    let [ totalPage , setTotalPage ] = useState(1);
    let [ totalCount , setTotalCount ] = useState(1);


    // 2.   [     ]
    useEffect( ()=>{
        axios.get('/board',{ params : pageInfo })
            .then( r => {
                console.log(r);
                setRows( r.data.boardDtoList ) //   
                setTotalPage(r.data.totalPage) //   
                setTotalCount(r.data.totalCount) //     
            })
            .catch( err => { console.log(err); })
    } , [pageInfo] ) // pageInfo {cno , page }   useEffect .

    // useEffect( ()=>{}  )             :  , 
    // useEffect( ()=>{} , [] )         :  1
    // useEffect( ()=>{} , [] )     :  ,      

    // 3.  
    const categoryChange = ( cno ) => { pageInfo.cno = cno; setPageInfo({ ...pageInfo }) }
    // [...] vs {...} :  /   

    // 4.   
    const selectPage = (e) =>{
        //console.log(e);
        //console.log(e.target);
        //console.log(e.target.value) // button   value  
        //console.log(e.target.innerHTML); //  button   html 
        console.log(e.target.outerText); //  button     text 
        pageInfo.page = e.target.outerText;
        setPageInfo({...pageInfo});
    }

    return (
    <Container>
        <div>   : {pageInfo.page} /    : {totalCount} </div>
        <div style={{ display:'flex' , justifyContent : 'space-between' , alignItems : 'center' }}>
            <CategoryList categoryChange = { categoryChange } />
            <a href="/board/write"><Button variant="outlined">   </Button></a>
        </div>
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }} aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell align="center" style={{ width:'8%' }}></TableCell>
                <TableCell align="center" style={{ width:'60%' }}></TableCell>
                <TableCell align="center" style={{ width:'10%' }}></TableCell>
                <TableCell align="center" style={{ width:'10%' }}></TableCell>
                <TableCell align="center" style={{ width:'10%' }}></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map((row) => (
                <TableRow  key={row.name}   sx={{ '&:last-child td, &:last-child th': { border: 0 } }}  >
                  <TableCell align="center" component="th" scope="row"> {row.bno} </TableCell>
                  <TableCell align="left">{row.btitle}</TableCell>
                  <TableCell align="center">{row.mname}</TableCell>
                  <TableCell align="center">{row.bdate}</TableCell>
                  <TableCell align="center">{row.bview}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
        <div style={ { display:'flex' , justifyContent : 'center', margin : '40px 0px' } }>
            {/* count :    */}
            <Pagination count={totalPage} color="primary" onClick={selectPage} />
        </div>
    </Container>
    );
}