// p.183  
import React, {useState} from 'react';
//   
import {ListItem, ListItemText, InputBase, Checkbox, ListItemSecondaryAction, IconButton} from '@mui/material';
//  
import DeleteOutlined from '@mui/icons-material/DeleteOutlined';
import axios from 'axios'; // npm install axios [ install -> i ]

export default function Todo(props) {

    // 1. Hook  useState
    const [ item, setItem ] = useState(props.item);

    // 2. props    
    const deleteItem = props.deleteItem;

    // 3.   
    const deleteEventHandler = ()=>{
        deleteItem(item);
    }


    // 4. readOnly = true   /       estReadOnly []
    const [readOnly , setReadOnly ] = useState(true);

    // 5.   
    const turnOffReadOnly = ()=>{ //console.log('turnOffReadOnly')
        setReadOnly(false); // readOnly = true  
    }

    // 6.   -  
    const turnOnReadOnly = (e)=>{ //console.log('turnOnReadOnly')
        if(e.key==="Enter"){
            setReadOnly(true);
            axios.put("/todo",{ id : item.id, done : item.done ,title : item.title }).then(r=>{console.log(r)});
        }
    }

    // 7.   
    let editItem = props.editItem
    const editEventHandler = (e)=>{ //console.log('editEventHandler')
    // inputBase   item.title 
        item.title = e.target.value;
        editItem();
    }

    // 8.  
    const checkboxEventHandler = (e)=>{
        item.done = e.target.checked; //checked :   true
        editItem(); //   
        axios.put("/todo",{ id : item.id, done : item.done ,title : item.title }).then(r=>{console.log(r)});
    }

    return (<>
        <ListItem>
            <Checkbox checked={item.done} onChange={checkboxEventHandler}  />
            <ListItemText>
                <InputBase
                    inputProps={{readOnly : readOnly}}
                    onClick = {turnOffReadOnly}
                    onKeyDown = {turnOnReadOnly}
                    onChange = {editEventHandler}
                    type="text"
                    id={`${item.id}`}
                    value={item.title}
                    multiline={true}
                    fullWidth={true}
                />
            </ListItemText>
            <ListItemSecondaryAction>
                <IconButton onClick={deleteEventHandler}>
                    <DeleteOutlined />
                </IconButton>
            </ListItemSecondaryAction>
        </ListItem>
    </>);
}