import React,{ useState , useEffect } from 'react';
import axios from 'axios';

import Container from '@mui/material/Container';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';

import CategoryList from './CategoryList'

export default function Write( props ) {

    let [cno, setCno] = useState(0);
    // 1.  
    const setBoard = () => {
        let info = {
            btitle : document.querySelector("#btitle").value,
            bcontent : document.querySelector("#bcontent").value,
            cno : cno
        }
        console.log( info );

        axios.post( '/board' , info )
            .then( r => {
                console.log(r.data);
                if(r.data==1){
                    alert('    .[]')
                }else if(r.data==2){
                    alert('  ')
                }else if(r.data==3){
                     alert('  ')
                 }else if(r.data==4){
                    alert('  ')
                    window.location.href="/board/list"
                 }
            })
    }
    //  
    const categoryChange = (cno)=>{ setCno(cno); }

    return(<>
        <Container>
            <CategoryList categoryChange={categoryChange} />
            <TextField fullWidth className="btitle"     id="btitle"  label="" variant="standard" />
            <TextField fullWidth className="bcontent"   id="bcontent" label="" multiline rows={10} variant="standard" />
            <Button variant="outlined" onClick={  setBoard }>  </Button>
            <Button variant="outlined">  </Button>
        </Container>
    </>)
}