import React,{ useState , useEffect } from 'react';
import axios from 'axios';
/* ----------- mui select ------------*/
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
/* -----------------------------------*/
export default function CategoryList( props ) {
    // 1.  /
    let [ list , setList ] = useState( [ ] ); // {}  vs []/
    useEffect( ()=>{
        axios.get('/board/category/list')
            .then( r => { setList(r.data ) });
    },[]) //  useEffect   1 
    // 2.  
    const [category, setCategory] = useState(0);

      const handleChange = (event) => {
        setCategory(event.target.value);
        props.categoryChange( event.target.value  )
      };

    return (<>
        <Box sx={{ minWidth: 120 }}>
          <FormControl style={{ width : '200px' , margin : '20px 0px'}}>
            <InputLabel id="demo-simple-select-label"></InputLabel>
            <Select  value={ category } label="" onChange={ handleChange } >
            <MenuItem value={0}></MenuItem>
            { /*       */ }
            {
                list.map( (c) => {
                    return   <MenuItem value={c.cno}>{ c.cname }</MenuItem>
                })
            }
            </Select>
          </FormControl>
        </Box>
    </> )
}